<template>
    <div class="Dashboard ">

    <div class="row mt-3">
      <div class="col-sm-4">
        <div class="card count-card">
            <p class="fs-2">Total Students</p>
            <p class="fs-1 fw-semibold  pb-3">{{ StudentCount }}</p>
          </div>
        
      </div>
            <div class="col-sm-4">
        <router-link to="/view-assignment">
        <div class="card count-card">
            <p class="fs-2">Total Assignments</p>
            <p class="fs-1 fw-semibold pb-3">{{ AssignmentCount }}</p>
          </div>
        </router-link>
      </div>
    
      <div class="col-sm-4">
       <router-link to="/submitted-assignment">
          <div class="card count-card">
            <p class="h2">Assignments Submitted</p>
            <p class="fs-1 fw-semibold"> {{ SubmittedAssignments }}</p>
          </div>
       </router-link>
      </div>
    </div>
  
      <router-link to="/view-assignment">
      <div class="row mt-3">
        <div class="card count-card">
          <p class="fs-2 fw-semibold">Last Assignment Added</p>
          <p class="fs-5">Assignment Name: {{LastAssignmentAdded.aheading}}</p>
          <p class="fs-5">Assignment Description: {{LastAssignmentAdded.adesc}}</p>
          <p class="fs-5">Assignment Done by students: {{SubmittedDataCount}}</p>

        </div>
      </div>
      </router-link>
      <router-link to="/view-notice">
      <div class="row mt-4">
        <div class="card count-card">
          <p class="h3">Last Notification</p>
          <p class="fs-5">{{LastNotificationAdded.ntitle}}</p>
        </div>
      </div>
      </router-link>
  </div>
</template>

<script>
 import { mapActions,mapGetters } from "vuex";
import axios from 'axios'
import config from "@/config"

export default {
name:'StudentDashboard',
  data(){
    return{
        students:[],
        StudentCount:"",
         Assignments:[],
        AssignmentCount:"",
        LastAssignmentAdded:"",
        acode:"",
        aheading:"",
        SubmittedAssignments:"",
        SubmittedDataCount:"",
        Ascode:this.acode,


        LastNotificationAdded:""
  }
},
computed:{
    ...mapGetters(["user"]),
},
methods:{
...mapActions(["getProfile"]),
  //Get Students list
        async getStudents() {
      try {
        let {
          data
        } = await axios.get(`${config.apiBaseUrl}/api/users/get-student`)

        this.students = data
        this.StudentCount = data.length
      } catch (error) {
        console.error(error)
      }

    },

// Get Assignment last added 
async getAssignments(){
  
    let {
    data
  } = await axios.get(`${config.apiBaseUrl}/api/assignments/get-assignments`)
  this.Assignments = data,
  this.AssignmentCount = data.length
  this.LastAssignmentAdded = data[data.length-1]

},


//Get submitted assignment by username
 async getSubmittedAssignments(){
     
        let usernames = this.user.username
        
        
let {
    data
}= await axios.get(`${config.apiBaseUrl}/api/submissions/get-submitted-assignment?username=${usernames}` )
  this.Assignments = data
  this.SubmittedAssignments = data.length




},
// Get Submitted Assignment By Acode
 async getAssignmentsByCode(){

    let acode = this.LastAssignmentAdded.acode
    
let {
    data
}= await axios.get(`${config.apiBaseUrl}/api/submissions/get-students-by-acode?acode=${acode}` )

  this.SubmittedData = data,
this.SubmittedDataCount = data.length
},

// Get notification lists
async getNotifications(){
  
    let {
    data
  } = await axios.get(`${config.apiBaseUrl}/api/notifications/get-notification`)
  this.Notifications = data
  this.LastNotificationAdded = data[data.length-1]

}

},
created(){
  this.getProfile();
  this.getStudents()
      this.getAssignments()
        this.getSubmittedAssignments()
    this.getNotifications()
    },
    beforeUpdate(){
this.getAssignmentsByCode()
        this.getSubmittedAssignments()
}
}

</script>

<style scoped>

.count-card {
  background:linear-gradient(127deg, #084298 0%, #7842f4 100%);
  padding: 10px 0px 0px 30px;
  height: fit-content;
  color: white;
}
a {
  text-decoration: none;
}
</style>